@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
/* HERO GRADIENT â€” BRIGHTER VERSION */
.hero-gradient {
  background:
    radial-gradient(
      circle at 70% 40%,
      rgba(255, 140, 255, 0.6) 0%,
      rgba(165, 90, 255, 0.45) 55%,
      rgba(50, 30, 100, 0.35) 80%,
      rgba(20, 15, 50, 0.25) 100%
    ),
    linear-gradient(
      135deg,
      #1a214f 0%,
      #292a63 33%,
      #403084 66%,
      #1c275c 100%
    );
  position: relative;
}

.hero-gradient::before {
  content: "";
  position: absolute;
  inset: 0;
  background: linear-gradient(
    to bottom right,
    rgba(255, 255, 255, 0.12),
    rgba(255, 255, 255, 0.03)
  );
  mix-blend-mode: overlay;
  pointer-events: none;
}

.active-link span {
  width: 100% !important;
}


